/**
*  less@import,
*  
*  less
*/
// begin
@primary: #00ad8b; //
@primaryhover: #00694f;
@primarycolor: #333; //
@color9: #999;
@color6: #666;
@black: #000;
@bordercolor: #ecf0f5; //border
@color8: #888;
@red: #ff471c; //
//
@fs16: 16px;
@fs14: 14px;
@fs12: 12px;
@fs13: 13px;
//1border,
@border-size-1: 1px solid @bordercolor;

/*15px*/
.padding-size(@size:15px) {
  padding: @size;
}

//
.widHeight(@width:px;@height:px) {
  width: @width;
  height: @height;
}

//,
.heightLine(@height:px;@line-height:px;@width:px;@text:center) {
  height: @height;
  line-height: @line-height;
  width: @width;
  text-align: @text;
}



//css
.bgImg(@url:'';@repeat:no-repeat;@position:'') {
  background-image: url('../images/@{url}');
  background-repeat: @repeat;
  background-position: @position;
}
//
.boder_radius(@radius) {
  -moz-border-radius: @radius; /* Firefox */
  -webkit-border-radius: @radius; /* Safari  Chrome */
  border-radius: @radius; /* Opera 10.5+, IE-CSS3IE */
}
//
.bg_img(@url:''){
  background-image:url("../../assets/images/@{url}");
  filter:"progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";
  background-size:100% 100%;
}
//
.white-bgcolor {
  background-color: #fff;
}

//
.stu-overstep-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// width , height
.middle-center {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

//
.white-space {
  .padding-size(20px);
  .white-bgcolor;
}

// begin
.stu-components-box {
  .margin-top-20 {
    margin-top: 20px;
  }
  .left-box {
    float: left;
  }
  .right-box {
    float: right;
  }
  > .left-box {
    width: 880px;
    .top-left {
      .white-space;
    }
    .bottom-left {
      .white-space;
      .margin-top-20;
    }
  }
  > .right-box {
    width: 300px;
    .top-right {
      .white-space;
    }
    .content-right {
      .margin-top-20;
      .white-space;
    }
    .bottom-right {
      .margin-top-20;
      .white-space;
    }
  }
}

// end



















